import axios from 'axios';
import store from '../../../../store/index';
// 路由管理
// 登入相關的 api
const ordersRequest = axios.create({
  baseURL: `${process.env.VUE_APP_API_PATH}${process.env.VUE_APP_API_UUID}/admin/ec/orders`,
});

ordersRequest.interceptors.request.use(
  confing => {
    store.commit('SET_LOADING', true);
    return confing;
  },
  error => {
    store.commit('SET_LOADING', false);
    return Promise.reject(error);
  },
);

//響應攔截
ordersRequest.interceptors.response.use(
  response => {
    store.commit('SET_LOADING', false);
    return response;
  },
  error => {
    store.commit('SET_LOADING', false);
    return Promise.reject(error);
  },
);

export default ordersRequest;
